/**
 * 
 * 1.  js error
 * 2. - hover
 * 3.  PV/UV
 * 4.  
 * 5.  
 * 6.  
 * mode
 * 1.dev:log&&print,querystringlog
 * 2.prod:log,
 * 3.close(default):no log&&no print,
 */
import {stringifyEach,isType,getCookie,setCookie} from'./util'

export default class log{
    constructor(props) {
        this.mode = props&&props.mode || ~location.search.indexOf('log')&&'dev' ||'close'
        this.data = {
            error:[],
            action:[],
            visit:{},
            request:[],
            perforemance:[],
            customized:[]
        }
        this.proxyData()
        this.logVisit()
        this.catchMethod = this.catchMethod.bind(this)
        this.catchClass = this.catchClass.bind(this)
        this.addError = this.addError.bind(this)
        this.errorTransAndAdd = this.errorTransAndAdd.bind(this)
        this.trackAction = this.trackAction.bind(this)
        window.onerror = this.onerror.bind(this) 
    }

    /**@description PV*/
    logVisit(){
        this.data.visit.pvid = getCookie('pvid') //'vid.sid',PVIDID,vid,sidid
        this.plusSid()
    }

    /**@description sid+1*/
    plusSid(){
        setCookie('pvid',(this.data.visit.pvid||'').replace(/\d+$/,n=>+n+1),365*24*60*60*1000)
    }

    /**
     * @description datadevlog
     */
    proxyData(){
        if(this.mode!=='dev'){
            return
        }
        const methodMap = {
            action:'log',
            error:'error'
        }
        Object.keys(this.data).forEach(type=>{
            this.data[type] = new Proxy(this.data[type],{
                set:function(target,key,value,receiver){
                    if(key!=='length'){
                        console[methodMap[type]||'log'](type,':',value);   
                    }
                    return Reflect.set(target, key, value, receiver);
                }
            })
        })
    }

    /**
     * @description ,uncaught error
     * @param {string} message 
     * @param {string} file 
     * @param {number} line 
     * @param {number} col 
     * @param {Error} error 
     */
    onerror(message, file, line, col, error){
        this.addError({message,file,line,col,stack:error&&error.stack})
    }

    /**
     * @description 
     * @param {object} target 
     * @param {string} name 
     * @param {object} descriptor 
     */
    catchMethod(target, name, descriptor){
        if(this.mode === 'close'){
            return descriptor
        }
        let method = descriptor.value
        let self = this //log
        descriptor.value = function(...args){
            try{
                return method.apply(this, args) //this
            }catch(e){
                self.errorTransAndAdd(e)
            }
        }
        return descriptor
    }
    /**
     * @description errorBoundary
     * @param {object} target 
     * @param {string} name 
     * @param {object} descriptor 
     */
    catchClass(target){
        if(this.mode === 'close'){
            return
        }
        let methods = Object.getOwnPropertyNames(target.prototype)
        let self = this
        for(let method of methods){
            if(['constructor'].includes(method)){
                continue
            }
            let f = target.prototype[method]
            if(isType('Function')(f)){
                target.prototype[method] = function(...args){
                    try{
                        return f.apply(this,args)
                    }catch(e){
                        self.errorTransAndAdd(e)  
                    }
                }
            }
        }
    }
    /**
     * @description error
     * @param {Error} e 
     */
    errorTransAndAdd(e){
        const {stack,message} = e 
        let col,line,file
        let tmp = (stack||'').match(/\((.+)\)/)
        if(tmp&&tmp.length&&tmp[1]){
            tmp = tmp[1].split(':')
            col = +(col || tmp.pop())
            line = +(line || tmp.pop())
            file = tmp.join(':')
        }
        this.addError({message,stack,file,line,col})
    }
    /**
     * @description error
     * @param {Object} param
     */
    addError({message, stack,file, line, col}){
        this.data.error.push({
            message,
            file,
            line,
            col,
            stack:stack.replace(/\n/g,'\\n'),
            time:+new Date()
        })
    }
    /**
     * @description 
     * @param {string} message 
     * @param {object} target 
     * @param {string} name 
     * @param {object} descriptor 
     */
    trackAction(message){
        let self = this
        return function(target, name, descriptor){
            if(self.mode === 'close'){
                return descriptor
            }
            let method = descriptor.value
            descriptor.value = function (...rest) {
                self.data.action.push({
                    message,
                    classN:target.constructor.name,
                    methodN:name,
                    args: JSON.stringify(stringifyEach(rest.slice(0, -1))).slice(1, -1),
                    time: +new Date()
                })
                return method.apply(this, rest)
            }
            return descriptor
        }
    }
}